<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process191"></a>

<p><br></p>
<p><span class="rvts17">召唤秃鹰（</span><span class="rvts50">Summon Condor</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:582,Y:201</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：羽毛掉落者（</span><span class="rvts20">Featherfall</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">说服秃鹰并成为它的主人</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1223.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">秃鹰位于一处塔上，需要从塔下的门进入。使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">可以得知这里因为宝藏发生过一起矮人窝内斗的事情。秃鹰的原主人已经死在了塔上。</span>
<br><span class="rvts21">说服秃鹰不单需要具备</span><span class="rvts20"></span><span class="rvts21">动物伙伴（</span><span class="rvts20">Pet Pal</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">天赋，而且还需要至少</span><span class="rvts20">5</span><span class="rvts21">点的劝告值（</span><span class="rvts20">Persuasion</span><span class="rvts21">）和</span><span class="rvts20">15</span><span class="rvts21">点的记忆值（</span><span class="rvts20">Memory</span><span class="rvts21">），并且队伍中的每个角色只有一次说服的机会，一旦说服失败将永久无法再次尝试。</span>
<br><span class="rvts21">如果此时达不到上述的条件，可以尝试回到复仇女神号上，使用船底三层那面可以洗天赋洗技能点的镜子，达到条件，再来说服它。秃鹰一旦被说服，会跟随说服它的主人，并且可以在战斗中被召唤出来。召唤出的秃鹰具有飞翔（地形位移），啄食（穿刺攻击并致盲）和扇风（消除地面烟云环境，并造成气元素伤害）三个技能，更倾向于以智力为主属性的法师类职业携带。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



